<template>
  <div class="burger" @click="toggle" :class="{active: menuState, closing: !menuState && menuState !== null}">
    <span class="burger-menu-piece"></span>
    <span class="burger-menu-piece"></span>
    <span class="burger-menu-piece"></span>
  </div>
</template>

<script>
export default {
  name: 'Burger',
  methods: {
    toggle () {
      this.$store.commit('toggleMenu')
    }
  },
  computed: {
    menuState () {
      return this.$store.state.menuState
    }
  }
}
</script>

<style scoped lang="scss">
.burger {
  position: relative;
  width: 29px;
  height: 21px;
  margin-left: 32px;
  display: none;
  @include respond-to(lg) {
    display: block;
  }
}

$menu-animation-duration: 400ms;
$menu-animation-timing: ease-in-out;

.burger-menu-piece {
  display: block;
  position: absolute;
  width: 100%;
  left: 0;
  border-top: 2px solid $c-black;
  transform-origin: 50% 50%;
  transition: transform $menu-animation-duration $menu-animation-timing;

  &:nth-child(1) {
    top: 0;
  }

  &:nth-child(2) {
    top: 10px;
    opacity: 1;
    transition: transform $menu-animation-duration $menu-animation-timing, opacity 0ms linear $menu-animation-duration / 2;
  }

  &:nth-child(3) {
    top: 20px;
  }

  .active & {

    &:nth-child(1) {
      animation: burger-open-top $menu-animation-duration $menu-animation-timing forwards;
    }

    &:nth-child(2) {
      opacity: 0;
      transition: transform $menu-animation-duration $menu-animation-timing, opacity 0ms linear $menu-animation-duration / 2;
    }

    &:nth-child(3) {
      animation: burger-open-bot $menu-animation-duration $menu-animation-timing forwards;
    }
  }

  .closing & {
    &:nth-child(1) {
      animation: burger-close-top $menu-animation-duration $menu-animation-timing forwards;
    }

    &:nth-child(3) {
      animation: burger-close-bot $menu-animation-duration $menu-animation-timing forwards;
    }
  }
}

@keyframes burger-open-top {
  50% {
    transform: translate3d(0, 10px, 0);
  }
  100% {
    transform: translate3d(0, 10px, 0) rotate(45deg);
  }
}

@keyframes burger-open-bot {
  50% {
    transform: translate3d(0, -10px, 0);
  }
  100% {
    transform: translate3d(0, -10px, 0) rotate(-45deg);
  }
}

@keyframes burger-close-top {
  0% {
    transform: translate3d(0, 10px, 0) rotate(45deg);
  }
  50% {
    transform: translate3d(0, 10px, 0) rotate(0deg);
  }
  100% {
    transform: translate3d(0, 0, 0);
  }
}

@keyframes burger-close-bot {
  0% {
    transform: translate3d(0, -10px, 0) rotate(-45deg);
  }
  50% {
    transform: translate3d(0, -10px, 0) rotate(0deg);
  }
  100% {
    transform: translate3d(0, 0, 0);
  }
}

</style>
