import Vue from 'vue'
import Vuex from 'vuex'
import firebase from 'firebase/app'
import 'firebase/firestore'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    products: [],
    cart: [],
    userAccessKey: null,
    discount: {
      percent: 0,
      active: [],
      status: {
        success: -1,
        msg: ''
      }
    },
    menuState: null
  },
  mutations: {
    updateProducts (state, response) {
      response.forEach((doc) => {
        state.products.push(doc.data())
      })
    },
    deleteProductOfCart (state, productId) {
      state.cart = state.cart.filter(item => item.id !== productId)
    },
    updateCartProductAmount (state, {
      productId,
      amount
    }) {
      const item = state.cart.find(item => item.id === productId)
      if (item) {
        item.amount = amount
      }
    },
    updateCart (state, product) {
      const existence = state.cart.findIndex(el => el.id === product.id)
      if (existence > -1) {
        state.cart[existence].amount += 1
      } else {
        state.cart.push(product)
      }
    },
    updateUserAccessKey (state, accessKey) {
      state.userAccessKey = accessKey
    },
    syncCart (state, items) {
      state.cart = items
    },
    updateDiscount (state, data) {
      state.discount = data
    },
    toggleMenu (state) {
      if (state.menuState !== null) {
        state.menuState = !state.menuState
      } else {
        state.menuState = true
      }
    }
  },
  getters: {
    cartTotalPrice (state) {
      return state.cart.reduce((acc, item) => (item.amount * item.price) + acc, 0)
    }
  },
  actions: {
    addProductToCart ({
      dispatch,
      commit
    }, product) {
      commit('updateCart', {
        ...product,
        amount: 1
      })
      dispatch('syncCartToDataBase')
    },
    deleteProductOfCart ({
      dispatch,
      commit
    }, productId) {
      commit('deleteProductOfCart', productId)
      dispatch('syncCartToDataBase')
    },
    syncCartToDataBase (context) {
      firebase.firestore().collection('usersCart').doc(context.state.userAccessKey).update({
        items: context.state.cart
      })
    },
    updateCartProductAmount ({
      dispatch,
      commit
    }, {
      productId,
      amount
    }) {
      commit('updateCartProductAmount', {
        productId,
        amount
      })
      if (amount < 1) {
        return
      }
      dispatch('syncCartToDataBase')
    },
    takeUserCart (context) {
      const userAccessKey = localStorage.getItem('userAccessKey')
      const db = firebase.firestore()

      function createDataBaseUser () {
        db.collection('usersCart').add({
          items: []
        }).then((docRef) => {
          localStorage.setItem('userAccessKey', docRef.id)
          context.commit('updateUserAccessKey', docRef.id)
        })
      }

      if (!userAccessKey) {
        createDataBaseUser()
      } else {
        db.collection('usersCart').doc(userAccessKey).get().then((docRef) => {
          if (!docRef.data()) {
            createDataBaseUser()
          } else {
            context.commit('syncCart', docRef.data().items)
            context.commit('updateUserAccessKey', userAccessKey)
          }
        })
      }
    },
    loadProducts (context) {
      return firebase.firestore().collection('products').get().then((querySnapshot) => {
        context.commit('updateProducts', querySnapshot)
      })
    },
    changePromoCode (context, promoCode) {
      const currentData = JSON.parse(JSON.stringify(context.state.discount))
      let existence,
        activity
      return firebase.firestore().collection('promocodes').get().then((querySnapshot) => {
        existence = querySnapshot.docs.findIndex(el => el.id === promoCode)
        activity = context.state.discount.active.includes(promoCode)
        if (existence > -1) {
          if (!activity) {
            currentData.percent += querySnapshot.docs[existence].data().percent
            currentData.active.push(promoCode)
            currentData.status.success = 1
            currentData.status.msg = `Promo code for ${currentData.percent}% applied`
          } else {
            currentData.status.success = 0
            currentData.status.msg = 'This code has already been applied'
          }
        } else {
          currentData.status.success = 0
          currentData.status.msg = 'Invalid promo code'
        }
        context.commit('updateDiscount', currentData)
        return existence
      })
    }
  }
})
