import Vue from 'vue'
import VueRouter from 'vue-router'

const MainPage = () => import('@/pages/MainPage')
const NotFoundPages = () => import('@/pages/NotFoundPages')
Vue.use(VueRouter)

const routes = [
  {
    name: 'main',
    component: MainPage,
    path: '/'
  },
  {
    name: 'notFound',
    component: NotFoundPages,
    path: '*'
  }
]

const router = new VueRouter({
  routes,
  scrollBehavior () {
    return {
      x: 0,
      y: 0
    }
  }
})

export default router
